import {mapState, mapGetters} from 'vuex';
import header2 from '@/components/header/header2.vue';
export default{
    name: 'myInformation',
    head() {
        return {
            title: '个人信息'
        };
    },
    data () {
        return {
            position: 'default',
            showPositionValue: true,
            isLogin: true
        };
    },
    computed: {
        ...mapState(['mobileNbr']),
        ...mapGetters(['doneTodosCount'])
    },
    beforeCreate() {
    },
    methods: {
    },
    components: {
        header2
    }
};



